import React              from 'react'
import { Link }           from 'gatsby'
import { graphql }        from 'gatsby'
import { useStaticQuery } from 'gatsby'
import styles             from './header.module.scss'

const Header = () => {
    const data = useStaticQuery(graphql`
    query {
        site {
          siteMetadata {
            title
          }
        }
      }
    `)

    return (
        <header className={styles.header}>

            <div className={styles.title}>
                {data.site.siteMetadata.title}
            </div>

            <nav>
                <ul className={styles.navList}>
                    <li> <Link className={styles.navItem} activeClassName={styles.navActiveItem} to="/"> Home </Link> </li>
                    {/* <li> <Link className={styles.navItem} activeClassName={styles.navActiveItem} to="/blog"> Blog </Link> </li> */}
                    {/* <li> <Link className={styles.navItem} activeClassName={styles.navActiveItem} to="/about"> About </Link> </li> */}
                    {/* <li> <Link className={styles.navItem} activeClassName={styles.navActiveItem} to="/resume"> Resume </Link> </li> */}
                    {/* <li> <Link className={styles.navItem} activeClassName={styles.navActiveItem} to="/contact"> Contact </Link> </li> */}
                </ul>
            </nav>

        </header>
    )
}

export default Header